import React from "react";
import { Link, useNavigate } from "react-router-dom";
import Header from "../../components/Navi";
import styled from "styled-components";
import Upload from "../../components/UploadImage";

const Container = styled.div`
  width: 90%;
  height: auto;
  border: solid;
  border-color: #bdbdbd;
  margin: 20px auto;
  padding: 10px 30px;
`;

const MedianContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center; /*   */
`;

const Detail = styled.div`
  display: flex;
  justify-content: center;
  align-items: center; /*   */
  padding: 14px;
  p {
    margin-right: 10px;
  }
  input {
    width: 85%;
    height: 36px;
  }
`;

const StyledBtn = styled.button`
  width: 100%;
  height: 50px;
  margin: 10px;
  background-color: #ffffff;
  border-color: #bdbdbd;
`;

const StyledInput = styled.input`
  width: 100%;
  height: 161px;
  border: 1px solid #000;
  background: rgba(96, 96, 96, 0);
`;

const ShopR = () => {
  const navigate = useNavigate();

  const move = (e) => {
    e.preventDefault();
    //  
    navigate("/mypages");
  };
  return (
    <>
      <Header />
      <Container>
        <h2>  </h2>
        <MedianContainer>
          <Upload />
          <div style={{ marginLeft: "20px", width: "60%" }}>
            <Detail style={{ marginTop: "10px" }}>
              <p style={{ width: "15%" }}>  : </p>
              <input />
            </Detail>
            <Detail style={{ marginTop: "10px" }}>
              <p style={{ width: "15%" }}>  : </p>
              <input />
            </Detail>
            <h4 style={{ margin: "10px" }}>   </h4>
            <div
              style={{
                margin: "0",
                display: "flex",
                justifyContent: "center",
                padding: "4px",
              }}
            >
              <StyledBtn> </StyledBtn>
              <StyledBtn> </StyledBtn>
            </div>
          </div>
        </MedianContainer>
        <h2>  :</h2>
        <StyledInput
          placeholder="   .
          \n
          (  500 )"
        ></StyledInput>
        <div style={{ width: "100%", display: "flex" }}>
          <StyledBtn onClick={move}> </StyledBtn>
        </div>
      </Container>
    </>
  );
};
export default ShopR;
