import React, { useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import Header from "../../components/Navi";
import styled from "styled-components";
import Upload from "../../components/UploadImage";
import { useSelector } from "react-redux";

const Container = styled.div`
  width: 90%;
  height: auto;
  border: solid;
  border-color: #bdbdbd;
  margin: 20px auto;
  padding: 10px 30px;
`;

const MedianContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center; /*   */
`;

const Detail = styled.div`
  display: flex;
  justify-content: center;
  align-items: center; /*   */
  padding: 0 14px;
  p {
    margin-right: 10px;
  }
  input {
    width: 55%;
    height: 36px;
  }
`;

const StyledBtn = styled.button`
  width: 60%;
  height: 50px;
  margin: 30px auto;
  background-color: #ffffff;
  border-color: #bdbdbd;
`;

const StyledInput = styled.input`
  width: 100%;
  height: 100px;
  border: 1px solid #000;
  background: rgba(96, 96, 96, 0);
`;

const FlowerR = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const [name, setName] = useState("");
  const [size, setSize] = useState("");
  const [price, setPrice] = useState("");
  const [engName, setEngName] = useState("");
  const [message, setMessage] = useState("");
  const reduxData = useSelector((state) => state.userS);

  const move = (e) => {
    e.preventDefault();
    sendData();
  };

  const reduxData2 = useSelector((state) => state.baseUrl);
  const baseUrl = reduxData2.url;

  const sendData = (e) => {
    //     
    if (!name || !size || !price || !engName || !message) {
      alert(" .");
      return;
    } else {
      //    ( 2)
      let apiUrl = "";
      if (state.to === "/board") {
        apiUrl = baseUrl.concat(
          "/api/business/RegisterFlower/",
          reduxData.name
        );
      } else {
        apiUrl = baseUrl.concat("/api/saveDiscardFlower/", reduxData.name);
      }

      console.log("    ", reduxData.name);
      console.log("apiUrl", apiUrl);
      const dataToSend = {
        name: name,
        size: Number(size),
        price: Number(price),
        engName: engName,
        message: message,
      };
      fetch(apiUrl, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(dataToSend),
      })
        .then((data) => {
          console.log("  ");
        })
        .catch((error) => {
          console.error("Error:", error);
        });

      navigate(state.to);
    }
  };

  return (
    <>
      <Header />
      <Container>
        <h2 style={{ margin: "10px" }}>
          {state.to === "/board" ? "" : ""}  
        </h2>
        <MedianContainer>
          <Upload />
          <div style={{ marginLeft: "20px", width: "60%" }}>
            <Detail style={{ marginTop: "0" }}>
              <p style={{ width: "40%" }}>
                {state.to === "/board" ? "" : ""}   () :{" "}
              </p>
              <input
                input_type="text"
                placeholder="  "
                onChange={(e) => {
                  setName(e.target.value);
                }}
              />
            </Detail>
            <Detail style={{ marginTop: "10px" }}>
              <p style={{ width: "40%" }}>
                {state.to === "/board" ? "" : ""}   () :{" "}
              </p>
              <input
                input_type="text"
                placeholder=" () "
                onChange={(e) => {
                  setEngName(e.target.value);
                }}
              />
            </Detail>
            <Detail style={{ marginTop: "10px" }}>
              <p style={{ width: "40%" }}>   : </p>
              <input
                input_type="number"
                placeholder="    "
                onChange={(e) => {
                  setSize(e.target.value);
                }}
              />
            </Detail>
            <Detail style={{ marginTop: "10px" }}>
              <p style={{ width: "40%" }}> () : </p>
              <input
                input_type="number"
                placeholder="   "
                onChange={(e) => {
                  setPrice(e.target.value);
                }}
              />
            </Detail>
          </div>
        </MedianContainer>
        <h2>  :</h2>
        <StyledInput
          input_type="text"
          placeholder="   .
                (  200 )"
          onChange={(e) => {
            setMessage(e.target.value);
          }}
        />

        <div style={{ width: "100%", display: "flex" }}>
          <StyledBtn onClick={move}> </StyledBtn>
        </div>
      </Container>
    </>
  );
};
export default FlowerR;
